
import { defineComponent } from 'vue';
import services from '../services/TaskService'
import { Task } from '../interfaces/Task';

export default defineComponent({
  name: 'TaskList',
  props: { },
  data(){
    return {
      tasks: [] as Task[]
      }
  },
  methods: {
    async loadTask(){
      const res = await services.getTasks()
      this.tasks = res.data
    }
  },
  mounted() {
    this.loadTask()
  }
});
